import React, { Component } from "react";
import DirectorImg from "./DirectorImg";
import DMessage from "./DMessage";

export class DirectorMessage extends Component {
  render() {
    return (
      <>
        <div className="container">
          <div className="container-fluid text-center shadow">
            <div
              className="h1 display-1 font-weight-bold text-success "
              style={{ paddingTop: "100px" }}
            >
              Director's Message
            </div>

            <div className="row">
              <div className="col">
                <div className="container mt-5 mb-5">
                  <DirectorImg></DirectorImg>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col">
                <DMessage></DMessage>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default DirectorMessage;
