import React from "react";

//Calling Bootstrap 4.5 css

//Calling Firebase config setting to call the data
import firebase from "../../utilities/firebase";

class Notice extends React.Component {
  constructor(props) {
    super(props);

    this.state = { Notice: [] };
  }

  componentDidMount() {
    firebase
      .database()
      .ref("Notice")
      .on("value", (snapshot) => {
        let Notice = [];
        snapshot.forEach((snap) => {
          // snap.val() is the dictionary with all your keys/values from the 'students-list' path
          Notice.push(snap.val());
        });
        this.setState({ Notice: Notice });
      });
  }

  render() {
    return (
      <>
        <div className="container" style={{minHeight:"86vh"}}>
          <h1
            className="h1 display-1 font-weight-bold text-success text-center "
            style={{ paddingTop: "100px" }}
          >
            Notice
          </h1>

          {this.state.Notice?.slice(0)
            .reverse()
            .map((data) => {
              return (
                <>
                  {" "}
                  <div class="jumbotron shadow-lg bg-transparent text-left">
                    <p class="fs-3 lead">{data.title}</p>
                    <p class="lead">{data.date}</p>
                    <hr />
                    <p>
                      <a
                        class="btn btn-success"
                        data-bs-toggle="collapse"
                        href={`#href${data.slno}`}
                        role="button"
                        aria-expanded="false"
                        aria-controls="collapseExample"
                      >
                        Description
                      </a>
                    </p>
                    <div class="collapse" id={`href${data.slno}`}>
                      <div class="card card-body mb-1">
                        <p style={{ fontSize: "12px" }}>{data.description}</p>
                      </div>
                    </div>

                    <a
                      style={{ fontSize: "14px" }}
                      class="btn btn-success"
                      target="_blank"
                      href={data.link}
                      role="button"
                    >
                      Link
                    </a>
                  </div>
                </>
              );
            })}
        </div>
      </>
    );
  }
}
export default Notice;
