// export default Placement;

import React from "react";

//Calling Bootstrap 4.5 css

//Calling Firebase config setting to call the data
import firebase from "../../utilities/firebase";

class Placement extends React.Component {
  constructor(props) {
    super(props);

    this.state = { Placement: [] };
  }

  componentDidMount() {
    firebase
      .database()
      .ref("Placement")
      .on("value", (snapshot) => {
        let Placement = [];
        snapshot.forEach((snap) => {
          // snap.val() is the dictionary with all your keys/values from the 'students-list' path
          Placement.push(snap.val());
        });
        this.setState({ Placement: Placement });
      });
  }

  render() {
    return (
      <>
        <div
          className="container-fluid text-center"
          style={{
            backgroundColor: "#fff",
            minHeight: "86vh",
          }}
        >
          <div
            className="h1 display-1 font-weight-bold text-success "
            style={{ paddingTop: "100px" }}
          >
            Latest Placement Records
          </div>
          <div className="container shadow bg-white ">
            <div className="table-sm table-hover mb-3">
              <table class="table bg-white" style={{ fontSize: "12px" }}>
                <thead>
                  <tr>
                    <th scope="col">Sl.No. </th>
                    <th scope="col">Name</th>
                    <th scope="col">Company</th>
                    <th scope="col">Designation</th>
                    <th scope="col">CTC</th>
                  </tr>
                </thead>

                <tbody id="demo">
                  {this.state.Placement?.map((data) => {
                    return (
                      <>
                        <tr>
                          <td>{data.slno}</td>
                          <td>{data.name}</td>
                          <td>{data.company}</td>
                          <td>{data.designation}</td>
                          <td>{data.ctc}</td>
                        </tr>
                      </>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </>
    );
  }
}
export default Placement;
