import React, { Component } from 'react';
import Clock from "react-live-clock";


export class Date extends Component {
    render() {
        
        return (
          <>
            <span className="text-danger  ">
              <Clock
                format={"dddd,h:mm A"}
                ticking={true}
                timezone={"Asia/Calcutta"}
              />
            </span>
          </>
        );
    }
}

export default Date
