import React, { Component } from "react";
import { Link } from "react-router-dom";
import { VscFilePdf } from "react-icons/vsc";
import { HoverGreenBg } from "../../utilities/Hover";

export class Brochure extends Component {
  render() {
    return (
      <>
        <Link
          to={{
            pathname:
              "https://iambhaskar21.github.io/tnp_demo/Brochure201920.pdf",
          }}
          target="_blank"
          className="lead text-decoration-none text-white"
        >
          <HoverGreenBg>
            <VscFilePdf /> &nbsp;Brochure
          </HoverGreenBg>
        </Link>
      </>
    );
  }
}

export default Brochure;
