import React from "react";
import background from "../../img/Alumni.jpg";
import MainNav from "./AlumniNav/MainNav";
const Alumni = () => {
  return (
    <>
      <div
        className="text-center bg-transparent "
        style={{ minHeight: "86vh" }}
      >
        <div
          className=" text-center"
          style={{
            backgroundImage: ` linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) ),url(${background})`,
            backgroundPosition: `contain`,
            backgroundRepeat: `no-repeat`,
            backgroundSize: `100vw 100vh`,
          }}
        >
          <div
            class="jumbotron bg-transparent text-white"
            style={{ zIndex: `10` }}
          >
            <h1 class="display-1 font-weight-bold p-5 ">
              Welcome to our Alumni Section
            </h1>
            <div class="dropdown show">
              <a
                class="btn btn-success dropdown-toggle"
                href="#"
                role="button"
                id="dropdownMenuLink"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                Register for 2021 Batch
              </a>

              <div class="dropdown-menu" aria-labelledby="dropdownMenuLink">
                <a
                  class="dropdown-item"
                  href="https://forms.gle/9jdmkeKUaXNz5DNA8" target="_blank"
                >
                  B.Tech
                </a>
                <a
                  class="dropdown-item"
                  href="https://forms.gle/qBpQcqmvBsv2R13z8" target="_blank"
                >
                  B.Des
                </a>
                <a
                  class="dropdown-item"
                  href="https://forms.gle/3ruZeai9om2QGdh89" target="_blank"
                >
                  Diploma
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className=" bg-transparent ">
          <MainNav />
        </div>
      </div>
    </>
  );
};

export default Alumni;
