import React, { Component } from "react";

import imgdefault from "../../../../../img/Alumni/Default/user-sm.png";

export class Table2009 extends Component {
  render() {
    return (
      <div>
        <table class="table table-hover table-responsive-sm">
          <thead class="table-dark ">
            <tr>
              <th scope="col">Sl. No.</th>
              <th scope="col">Image</th>
              <th scope="col">Name</th>
              <th scope="col">Branch</th>
              <th scope="col">Current Job</th>
              <th scope="col">Contact</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>1</th>
              <td>
                {" "}
                <img
                  src={imgdefault}
                  className="rounded-circle d-block bg-dark"
                  alt=""
                  width="75"
                  height="75"
                />
              </td>
              <td>Anjalu Boro</td>
              <td>Instrumentation Engineering</td>
              <td>ONGC Assam Assets </td>
              <td></td>
            </tr>
           
                
          </tbody>
        </table>
      </div>
    );
  }
}

export default Table2009;
