import React, { useState } from "react";

import Header from "../src/components/Header/Header";
import Sidebar from "../src/components/Sidebar/Sidebar";
import Hero from "../src/components/Hero/Hero";
import Footer from "../src/components/Footer/Footer";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import About from "../src/components/AboutSection/About";
import Contact from "../src/components/ContactSection/Contact";
import Placement from "../src/components/PlacementSection/Placement";
import Activities from "../src/components/ActivitiesSection/Activities";
import Notice from "../src/components/NoticeSection/Notice";
import Alumni from "../src/components/AlumniSection/Alumni";
import RecruiterSection from "./components/RecruiterSection/RecruiterSection";

import DirectorMessage from "./components/Hero/DirectorMessage";
import TnpComponent from "./components/Hero/TnpComponent";
import PreviousMember from "./components/ContactSection/PreviousMember";

import PageNavigtionListener from "./utilities/PageNavigtionListener";
import NoMatch from "./components/NoMatch/NoMatch";

const Home = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => [setIsOpen(!isOpen), console.log("works bro")];

  const propy = "pass";
  return (
    <div>
      <Router>
        <PageNavigtionListener />
        <Header toggle={toggle} pass={propy} isOpen={isOpen}></Header>
        <Sidebar toggle={toggle} pass={propy} isOpen={isOpen}></Sidebar>

        <Switch>
          <Route path="/" exact>
            <Hero />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/placement">
            <Placement />
          </Route>
          <Route path="/members">
            <Contact />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/activities">
            <Activities />
          </Route>
          <Route path="/notice">
            <Notice />
          </Route>
          <Route exact path="/recruitment">
            <RecruiterSection />
          </Route>
          <Route exact path="/alumni">
            <Alumni />
          </Route>
          <Route exact path="/directormessage">
            <DirectorMessage />
          </Route>
          <Route exact path="/tnpmessage">
            <TnpComponent />
          </Route>
          <Route exact path="/previousmembers">
            <PreviousMember />
          </Route>
         
        </Switch>

        <Footer isOpen={isOpen}></Footer>
      </Router>
    </div>
  );
};

export default Home;
