import React, { Component } from 'react'
import defaultimg from "../../img/people/director.jpg";
export class DirectorImg extends Component {
    render() {
        return (
          <div>
            <img
              src={defaultimg}
              alt=""
              className="rounded-circle mx-auto d-block "
              width="150"
            />
          </div>
        );
    }
}

export default DirectorImg
