import React from "react";

import background from "../../img/Recruitment.jpg";
import Brochure from "./Brochure";
import PastRecruiter from "./PastRecruiter";
import Policy from "./Policy";
import Registration from "./Registration";

function RecruiterSection() {
  return (
    <>
      {" "}
      <div className=" bg-light " style={{ minHeight: "86vh" }}>
        {" "}
        <div
          className="container-fluid text-center"
          style={{
            backgroundImage: ` linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) ),url(${background})`,
            backgroundPosition: `contain`,
            backgroundRepeat: `no-repeat`,
            backgroundSize: `100vw 100vh`,
          }}
        >
          <div
            class="jumbotron bg-transparent text-white"
            style={{ zIndex: `10` }}
          >
            <h1 class="display-1 font-weight-bold p-5 ">
              CIT Kokrajhar Recruitment Section
            </h1>
          </div>
        </div>
        {/* content */}
        <div className="container-fluid">
          <div className="row  justify-content-center">
            <div className="col-md-3 text-center">
              <div class="card m-4 shadow p-4 bg-success">
                <div class="card-body text-white">
                  <Brochure />
                </div>
              </div>
            </div>
            <div className="col-md-3 text-center">
              <div class="card m-4 shadow p-4 bg-success">
                <div class="card-body text-white">
                  <Registration />
                </div>
              </div>
              {/* <Policy /> */}
            </div>
          </div>
          <div className="row  p-2">
            <div className="col text-center">
              <PastRecruiter />
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default RecruiterSection;