import React from "react";
import firebase from "../../utilities/firebase";
import {GiCondyluraSkull} from "react-icons/gi"
import { Link } from "react-router-dom";
import { Hover } from "../../utilities/Hover";
import Date from "./Date";
class ScrollSection extends React.Component {
  constructor(props) {
    super(props);

    this.state = { Notice: [] };
  }

  componentDidMount() {
    firebase
      .database()
      .ref("Notice")
      .on("value", (snapshot) => {
        let Notice = [];
        snapshot.forEach((snap) => {
          Notice.push(snap.val());
        });
        this.setState({ Notice: Notice });
      });
  }

  render() {
    return (
      <>
        <div className="row border-bottom fs-6 lead  text-center  pt-2">
          <p>
           Latest Updates : <Date/>
          </p>
        </div>
        <div className="row">
          <div class="overflow-auto text-left " style={{ height: "150px" }}>
            {" "}
            {this.state.Notice?.slice(0)
              .reverse()
              .map((data) => {
                return (
                  <>
                    <div className=" container lead fs-6 ">
                      <div className="row">
                        <div className="col-1">
                          <span className="text-danger">
                            <GiCondyluraSkull />
                          </span>
                        </div>{" "}
                        &nbsp;
                        <div className="col-10 fs-6">
                          {" "}
                          <span>
                            <a
                              href={data.link}
                              className="text-decoration-none text-dark"
                              target="_blank"
                             
                            >
                              <Hover>{data.title}</Hover>
                            </a>
                          </span>
                        </div>
                      </div>
                    </div>
                  </>
                );
              })}
          </div>
        </div>

        <div className="row border-top lead  text-center fs-6  pt-2">
          <p>
            {" "}
            Visit{" "}
            <Link to="/notice" className=" text-success">
              {" "}
              Notice Section
            </Link>{" "}
            Section for More Details
          </p>
        </div>
      </>
    );
  }
}
export default ScrollSection;
