import React, { Component } from "react";

export class NavTopics extends Component {
  render() {
    return (
      <>
        <center>
          <nav>
            <div
              class="nav nav-tabs bg-transparent d-flex justify-content-center "
              id="nav-tab"
              role="tablist"
            >
              <button
                class="nav-link "
                id="nav-mtech-tab"
                data-bs-toggle="tab"
                data-bs-target="#nav-mtech"
                type="button"
                role="tab"
                aria-controls="nav-mtech"
                aria-selected="true"
              >
                M.Tech
              </button>
              <button
                class="nav-link active "
                id="nav-btech-tab"
                data-bs-toggle="tab"
                data-bs-target="#nav-btech"
                type="button"
                role="tab"
                aria-controls="nav-btech"
                aria-selected="false"
              >
                B.Tech
              </button>
              <button
                class="nav-link"
                id="nav-mdes-tab"
                data-bs-toggle="tab"
                data-bs-target="#nav-mdes"
                type="button"
                role="tab"
                aria-controls="nav-mdes"
                aria-selected="false"
              >
                M.Des
              </button>
              <button
                class="nav-link"
                id="nav-bdes-tab"
                data-bs-toggle="tab"
                data-bs-target="#nav-bdes"
                type="button"
                role="tab"
                aria-controls="nav-bdes"
                aria-selected="false"
              >
                B.Des
              </button>
              <button
                class="nav-link"
                id="nav-diploma-tab"
                data-bs-toggle="tab"
                data-bs-target="#nav-diploma"
                type="button"
                role="tab"
                aria-controls="nav-diploma"
                aria-selected="false"
              >
                Diploma
              </button>
              <button
                class="nav-link"
                id="nav-phd-tab"
                data-bs-toggle="tab"
                data-bs-target="#nav-phd"
                type="button"
                role="tab"
                aria-controls="nav-phd"
                aria-selected="false"
              >
                PhD
              </button>
            </div>
          </nav>
        </center>
      </>
    );
  }
}

export default NavTopics;
