import React, { Component } from "react";
import Btech from "./BTech/Btech";
import Diploma from "./Diploma/Diploma";

export class NavContent extends Component {
  render() {
    return (
      <>
        <div class="tab-content pt-2" id="nav-tabContent" style={{ width: "90vw" }}>
          <div
            class="tab-pane fade"
            id="nav-mtech"
            role="tabpanel"
            aria-labelledby="nav-mtech-tab"
          >
            M.Tech
          </div>
          <div
            class="tab-pane fade show active"
            id="nav-btech"
            role="tabpanel"
            aria-labelledby="nav-btech-tab"
          >
            <div className="container  " >
              <Btech />
            </div>
          </div>
          <div
            class="tab-pane fade"
            id="nav-mdes"
            role="tabpanel"
            aria-labelledby="nav-mdes-tab"
          >
            M.Des
          </div>
          <div
            class="tab-pane fade"
            id="nav-bdes"
            role="tabpanel"
            aria-labelledby="nav-bdes-tab"
          >
            B.Des
          </div>
          <div
            class="tab-pane fade"
            id="nav-diploma"
            role="tabpanel"
            aria-labelledby="nav-diploma-tab"
          >
            <Diploma />
          </div>
          <div
            class="tab-pane fade"
            id="nav-phd"
            role="tabpanel"
            aria-labelledby="nav-phd-tab"
          >
            PhD
          </div>
        </div>
      </>
    );
  }
}

export default NavContent;
